* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-image {
  height: 50px;
  width: 50px;
  margin-right: 10px;
}
.country-item {
  display: block;
}
.country-title,
.country-text {
  font-size: 18px;
}
.country-list {
  display: block;
}
.country-about {
  display: flex;
}
.country-span {
  font-weight: bold;
}
